@font-face {
    font-family: aerospace;
    src: url(./../res/fonts/Aerospace.ttf);
}

@font-face {
    font-family: mono-display;
    src: url(./../res/fonts/MajorMonoDisplay-Regular.ttf);
}

$pallette-1: #333;
$pallette-2: #ddd;

@media only screen and (max-width: 600px) {
    h1 {font-size: 35px }
    p { line-height: 1rem; padding-bottom: 30px }
    h2 { margin-bottom: 2rem }
}

@media only screen and (min-width: 600px) {
    h1 {font-size: 35px }
    p { line-height: 1rem; padding-bottom: 30px }
    h2 { margin-bottom: 2rem }
}

@media only screen and (min-width: 768px) {
    h1 {font-size: 50px }
    p { line-height: 1.5rem }
    h2 { margin-bottom: 0.5rem }
}

@media only screen and (min-width: 992px) {
    h1 {font-size: 50px }
    p { line-height: 2rem }
    h2 { margin-bottom: 2rem }
}

body {
    background-color: $pallette-1;
    background-image: url(../res/pictures/bg.png);
    font-family: mono-display;
    margin: 0;
}

p {
    font-size: 0.8rem;
    font-weight: bold;
    color: white;
}

h1 {
    color: white;
    font-family: aerospace;
}

h2 {
    font-family: mono-display;
    font-size: 3rem;
    font-weight: 200;
    color: white;
}

h3 {
    font-family: mono-display;
    font-size: 2rem;
    font-weight: 200;
    color: white;
}

img {
    object-fit: contain;
}

#footer {
    background-color: black;
    margin: 0;
    padding: 10px;
    font-size: x-small;
}

#freepik-attr {
    color: white;
    margin: 0;
    :visited {
        color: $pallette-2;
    }
}

@import 'Header.scss';
@import 'Landing.scss';
@import 'Sawy.scss';